.am-button{
    display: flex;
    justify-content:center;
    align-items: center;
    width: 3rem;
    height:0.44rem;
    background:rgba(52,90,189,1);
    border-radius:0.25rem;
    font-size: 0.17rem;
    color: #fff;
}
.am-button:active {
    opacity: 0.8;
}
.gray-btn{
    background: rgba(52,90,189,0.1) !important;
    color: #1B53E5 !important;
}
.disabled-btn {
    background: #AAAAAAFF !important;
    pointer-events: none;
}
.text-btn{
    width: auto;
    padding: 0 0.1rem;
    background: transparent;
    color:#345ABD;
}
.am-button span{
   color:inherit;
}
