body{margin:0;font-family:STHeiti,'微软雅黑';-webkit-font-smoothing:antialiased;-webkit-text-size-adjust:none}body,dd,dl,form,h1,h2,h3,h4,h5,h6,p{margin:0}html{overflow-Y:auto}li,ol,ul{margin:0;padding:0}li,ol{list-style:none}button{display:block;background:#f5f5f5;border:none;cursor:pointer;outline:0;font-weight:700;-webkit-tap-highlight-color:transparent}input,select{outline:0;-moz-outline-style:none;border:none;-webkit-tap-highlight-color:transparent;background:#fff}img{border:0;width:100%;height: 100%;}a{color:#333;text-decoration:none;-webkit-text-size-adjust:none;-webkit-tap-highlight-color:transparent}.word{word-break:break-all}.width_fixed{white-space:nowrap;text-overflow:ellipsis;overflow:hidden}table{border-left:1px solid #dedede;border-top:1px solid #dedede}
html,body,#root{
    height: 100%;
}
html body {
    padding:0;
}


body{
    font-size: 16px;
    font-family: PingFangSC-Regular, PingFang SC;
}
.App{
    display: flex;
    min-height: 100%;
}

* {
    box-sizing: border-box;
}
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button{
    -webkit-appearance: none;
}
input[type="number"]{
    -moz-appearance: textfield;
}
